import Sequelize from 'sequelize';
import config from '../config';

const sequelizeProvider = async () => {
  try {
    const { DBNAME, DBUSER, DBPASS, DBHOST, DB } = config.getSequelizeConfig();
    const sequelize = await new Sequelize(DBNAME, DBUSER, DBPASS, { host: DBHOST, dialect: DB });
    sequelize.sync();
    return sequelize;
  } catch (error) {
    console.error(error);
  }
};

const Op = Sequelize.Op;
const sequelize = sequelizeProvider();

export { sequelize, Op };
